/* Navbar.css */
.navbar {
  display: flex;
  gap: 15px;
  padding: 15px;
  background-color: #e0f7ff; /* —Å–≤–µ—Ç–ª—ã–π –≥–æ–ª—É–±–æ–π —Ñ–æ–Ω –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin: 20px;
}

.nav-btn {
  text-decoration: none;
  color: #007bff; /* –æ—Å–Ω–æ–≤–Ω–æ–π —Å–∏–Ω–∏–π —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ */
  font-weight: 600;
  padding: 10px 18px;
  border-radius: 8px;
  transition: all 0.3s ease;
  background-color: #ffffff; /* –±–µ–ª—ã–π —Ñ–æ–Ω –∫–Ω–æ–ø–∫–∏ */
  border: 2px solid #007bff;
}

.nav-btn:hover {
  background-color: #007bff; /* —Å–∏–Ω–∏–π —Ñ–æ–Ω –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
  color: #ffffff; /* –±–µ–ª—ã–π —Ç–µ–∫—Å—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
  transform: scale(1.05);
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.3);
}